@charset "UTF-8";
@font-face {
  font-family: 'nunito_sansextralight';
  src: url("../font_nunito_sans_light/nunitosans-extralight-webfont.woff2") format("woff2"), url("../font_nunito_sans_light/nunitosans-extralight-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

.home {
  background-color: #2c3e50;
  height: 100vh;
  width: 80%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center; }

.home-content {
  font-family: 'nunito_sansextralight';
  color: white;
  padding: 0 100px; }
  .home-content h1 {
    font-weight: 100;
    font-size: 86px;
    margin: 0px; }

span {
  font-size: 28px;
  background-color: white;
  padding: 2px 5px;
  color: #2c3e50; }

.period {
  font-size: 18px; }

/* Små skärmar */
@media screen and (max-width: 900px) {
  .home {
    width: 100%; }
  .home-content {
    padding: 0 20px; }
    .home-content h1 {
      font-size: 52px;
      margin: 0px; }
  span {
    font-size: 24px; }
  .period {
    font-size: 16px; } }



